import React from 'react'
import ReactDOM from 'react-dom'
import {timezones} from "./timezones";

class MyTimezonePicker extends React.Component {
    constructor(props) {
        super(props);
        this.timezoneOptions = timezones.map(timezone =>
            <option value={timezone.offset}>
                {timezone.text}
            </option>
        );
        this.state = {offset: -12};
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.setState({offset: event.target.value}, () => {
            this.props.offsetHandler(this.state.offset);
        });
    }
    render() {
        return (
            <select onChange={this.handleChange}>
                {this.timezoneOptions}
            </select>
        )
    }
}


class Clock extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            date: new Date(),
            offsetDate: new Date(),
        };
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(),
            1000
        );
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    tick() {
        let OD = new Date();
        OD.setHours(OD.getUTCHours()  + parseInt(this.props.offset));
        this.setState({date: new Date()});
        this.setState( {offsetDate: OD});
    }

    render() {
        return (
            <div>
                <h2>It is {this.state.date.toLocaleTimeString()} in Moscow.</h2>
                <h2>It is {this.state.offsetDate.toLocaleTimeString()} in</h2>
            </div>
        );
    }
}

class App extends React.Component {
    constructor (props) {
        super(props);
        this.state = {offset: 0};
    }

    render() {
        return (
            <div className="App">
                <Clock offset={this.state.offset}/>
                <MyTimezonePicker offsetHandler={off => {
                    this.setState({offset: off})
                }}/>
            </div>
        )
    }
}

ReactDOM.render(
    <App/>,
    document.getElementById('root')
);